.container{
  width:100%;
  height: 100%;
  position: relative;
  .topContainer{
    background-color:#E6EAF0;
    .top{
      height:120px;
      width:1180px;
      margin:0 auto;
      position: relative;
      :global(.ant-breadcrumb){
        text-align: left;
        width:200px;
        position: absolute;
        top:34px;
        font-size: 16px;
        :global(.anticon-shopping){
          margin-right: 8px;
        }
      }
      .right{
        position: absolute;
        right:165px;
        text-align: right;
        width:50%;
        line-height:120px;
        .add{
          width:102px;
          height:40px;
          margin-right:3.9%;
          background-color:#6597FF;
          font-size: 14px;
          font-family: Roboto;
          vertical-align: middle;
        }
      }
    }
  }
  :global(.ant-tabs-nav-wrap){
    text-align: left;
    background-color: #E6EAF0;

  }
  :global(.ant-tabs) {
    width: 100%;
    margin: -40px 0 0;
    background-color: #E6EAF0
  }
  :global(.ant-tabs-card) {
    border: none;

    :global( .ant-tabs-content) {

      :global(.ant-tabs-tabpane) {
        background: #fff;
        padding-top: 24px;
        width:100%;
        height:100%;
      }
    }

    :global(.ant-tabs-bar) {
      width: 1196px;
      margin: 0 auto;
      border-color: #fff;


      :global(.ant-tabs-tab) {
        width: 100px;
        height: 40px;
        text-align: center;
        border: none;
        background: #F0F2F5;
        margin-right: 5px;
      }

      :global(.ant-tabs-tab-active) {
        border: none;
        background: #fff;
      }
    }
  }
  //@media screen and (max-width: 1494px) {
  //  .container{
  //    :global(.ant-pagination-total-text) {
  //      margin-right:30%!important;
  //    }
  //  }
  //}
  .tabContainer{
    padding:0 3%;
    height: 100%;
    color:#666666;
    font-size: 14px;
    .top,.center,.bottom  {
      width:788px;
      margin:0 auto;
    }
    .title{
      text-align: left;
      color:#333333;
      font-size: 16px;
    }
    .bottom{
      margin-bottom:53px;
      .configure{
        float: left;
        width:170px;
        text-align: right;
      }
    }
    :global(.ant-form-item-label label){
      color:#666666;
    }

    .table {
      background-color: #FFFFFF;
      width: 426px;
      margin-left:197px ;

      :global(.ant-table-thead) {
        background-color: #F0F2F5;
      }

      :global(.ant-table-thead tr th) {
        background-color: #F0F2F5;
      }
      :global(.ant-table-column-sorter) {
        color: #101010 !important;
      }

      :global(.ant-table-tbody tr:nth-child(even)) {
        background-color: rgba(240, 242, 245, 0.5);
      }
      :global(.ant-table-thead  tr   th){
        padding: 10px 8px !important;
      }
      :global(.ant-select-lg) {
        width:200px;
      }
      td, div {
        color: rgba(16, 16, 16, 1);
        font-family: SourceHanSansSC-regular;
        font-size: 14px;
      }
      td{
        padding: 10px 8px !important;

      }

      a {
        color: rgba(101, 151, 255, 1);
        text-decoration: none;
      }
    }
    .prompt{
      float:right;
      padding: 15px 0 0 9px;
      width: 164px;
      text-align: left;
    }
  }

}